import { useState } from 'react';
import { School } from '@/types';

interface SchoolListProps {
  schools: School[];
  selectedSchool: School | null;
  setSelectedSchool: (school: School | null) => void;
  isCollapsed?: boolean;
  setIsCollapsed?: (collapsed: boolean) => void;
}

export default function SchoolList({ 
  schools, 
  selectedSchool, 
  setSelectedSchool,
  isCollapsed: externalIsCollapsed,
  setIsCollapsed: externalSetIsCollapsed
}: SchoolListProps) {
  // Use internal state if no external state is provided
  const [internalIsCollapsed, setInternalIsCollapsed] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  
  // Use either external or internal state
  const isCollapsed = externalIsCollapsed !== undefined ? externalIsCollapsed : internalIsCollapsed;
  const setIsCollapsed = externalSetIsCollapsed || setInternalIsCollapsed;

  // Filter schools based on search query
  const filteredSchools = searchQuery.trim() === '' 
    ? schools 
    : schools.filter(school => {
        const query = searchQuery.toLowerCase();
        return (
          school.name.toLowerCase().includes(query) ||
          school.district.toLowerCase().includes(query) ||
          school.address.toLowerCase().includes(query) ||
          school.city.toLowerCase().includes(query) ||
          school.state.toLowerCase().includes(query) ||
          school.zipCode.toLowerCase().includes(query)
        );
      });
  
  const schoolCount = filteredSchools.length;
  const geocodingPendingCount = filteredSchools.filter(school => 
    !school.latitude || !school.longitude
  ).length;
  
  return (
    <div className="h-full overflow-hidden bg-white rounded-lg shadow border border-gray-200 flex flex-col">
      <div 
        className="sticky top-0 bg-white p-4 border-b border-gray-200 flex justify-between items-center cursor-pointer hover:bg-gray-50"
        onClick={() => setIsCollapsed(!isCollapsed)}
      >
        <div>
          <h2 className="font-bold text-lg text-black">Schools ({schools.length})</h2>
          {geocodingPendingCount > 0 && (
            <p className="text-xs text-gray-500 mt-1">
              {geocodingPendingCount} school{geocodingPendingCount !== 1 ? 's' : ''} awaiting geocoding
            </p>
          )}
        </div>
        <button 
          className="text-gray-700 hover:text-black transition-colors flex items-center gap-1"
          aria-label={isCollapsed ? "Expand school list" : "Collapse school list"}
        >
          {isCollapsed ? (
            <>
              <span className="text-xs hidden sm:inline">Expand</span>
              <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor" className="w-5 h-5">
                <path fillRule="evenodd" d="M16.28 11.47a.75.75 0 010 1.06l-7.5 7.5a.75.75 0 01-1.06-1.06L14.69 12 7.72 5.03a.75.75 0 011.06-1.06l7.5 7.5z" clipRule="evenodd" />
              </svg>
            </>
          ) : (
            <>
              <span className="text-xs hidden sm:inline">Collapse</span>
              <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor" className="w-5 h-5">
                <path fillRule="evenodd" d="M7.72 12.53a.75.75 0 010-1.06l7.5-7.5a.75.75 0 111.06 1.06L9.31 12l6.97 6.97a.75.75 0 11-1.06 1.06l-7.5-7.5z" clipRule="evenodd" />
              </svg>
            </>
          )}
        </button>
      </div>
      
      {!isCollapsed && (
        <>
          {schools.length > 0 && (
            <div className="px-3 pt-3">
              <div className="relative">
                <input
                  type="text"
                  placeholder="Search schools..."
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  className="w-full p-2 pl-8 border border-gray-300 rounded text-sm focus:ring-gray-500 focus:border-gray-500"
                />
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  className="h-4 w-4 absolute left-2.5 top-2.5 text-gray-400"
                  fill="none"
                  viewBox="0 0 24 24"
                  stroke="currentColor"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
                  />
                </svg>
                {searchQuery && (
                  <button
                    onClick={() => setSearchQuery('')}
                    className="absolute right-2.5 top-2.5 text-gray-400 hover:text-gray-600"
                  >
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      className="h-4 w-4"
                      fill="none"
                      viewBox="0 0 24 24"
                      stroke="currentColor"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth={2}
                        d="M6 18L18 6M6 6l12 12"
                      />
                    </svg>
                  </button>
                )}
              </div>
              {searchQuery && (
                <p className="mt-2 text-xs text-gray-600">
                  Found {schoolCount} {schoolCount === 1 ? 'school' : 'schools'} matching "{searchQuery}"
                </p>
              )}
            </div>
          )}
          
          <div className="overflow-auto flex-grow">
            {schools.length === 0 ? (
              <div className="p-4 text-center text-gray-700">
                <svg xmlns="http://www.w3.org/2000/svg" className="h-12 w-12 mx-auto mb-3 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1} d="M19 21V5a2 2 0 00-2-2H7a2 2 0 00-2 2v16m14 0h2m-2 0h-5m-9 0H3m2 0h5M9 7h1m-1 4h1m4-4h1m-1 4h1m-5 10v-5a1 1 0 011-1h2a1 1 0 011 1v5m-4 0h4" />
                </svg>
                <p className="font-medium">No schools imported yet</p>
                <p className="text-sm mt-1">Upload a CSV file to get started</p>
              </div>
            ) : filteredSchools.length === 0 ? (
              <div className="p-4 text-center text-gray-700">
                <p>No schools match your search</p>
                <button 
                  className="mt-2 text-sm text-gray-700 hover:text-black underline"
                  onClick={() => setSearchQuery('')}
                >
                  Clear search
                </button>
              </div>
            ) : (
              <ul className="divide-y divide-gray-200 px-1 py-2">
                {filteredSchools.map((school) => (
                  <li 
                    key={school.id || `${school.name}-${school.address}`}
                    className={`p-3 hover:bg-gray-50 cursor-pointer transition-colors rounded mx-1 my-0.5 ${
                      selectedSchool && selectedSchool.name === school.name && 
                      selectedSchool.address === school.address 
                        ? 'bg-gray-100 border-l-4 border-gray-400' 
                        : ''
                    }`}
                    onClick={() => setSelectedSchool(school)}
                  >
                    <h3 className="font-medium text-black">{school.name}</h3>
                    <p className="text-sm text-gray-700">{school.district}</p>
                    <p className="text-sm text-gray-900 mt-1">{school.address}</p>
                    <p className="text-sm text-gray-900">{school.city}, {school.state} {school.zipCode}</p>
                    {!school.latitude && !school.longitude && (
                      <span className="inline-block mt-2 px-2 py-1 bg-gray-100 text-gray-800 text-xs rounded border border-gray-300">
                        <svg xmlns="http://www.w3.org/2000/svg" className="h-3 w-3 inline-block mr-1" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
                        </svg>
                        Geocoding pending
                      </span>
                    )}
                  </li>
                ))}
              </ul>
            )}
          </div>
        </>
      )}
    </div>
  );
} 