import { useState } from 'react';
import { fetchSchoolsFromHubSpot, importSchoolsToHubSpot } from '@/utils/hubspot';
import { School } from '@/types';

interface HubSpotIntegrationProps {
  schools: School[];
  onSchoolsLoaded: (schools: School[]) => void;
  setLoading: (loading: boolean) => void;
}

export default function HubSpotIntegration({ 
  schools, 
  onSchoolsLoaded, 
  setLoading 
}: HubSpotIntegrationProps) {
  const [apiKey, setApiKey] = useState('');
  const [showApiInput, setShowApiInput] = useState(false);
  const [message, setMessage] = useState<{ text: string; type: 'success' | 'error' | 'info' } | null>(null);

  // Fetch schools from HubSpot
  const handleFetchFromHubSpot = async () => {
    if (!apiKey) {
      setMessage({ text: 'Please enter your HubSpot API key', type: 'error' });
      return;
    }
    
    try {
      setLoading(true);
      setMessage({ text: 'Fetching schools from HubSpot...', type: 'info' });
      
      const hubspotSchools = await fetchSchoolsFromHubSpot(apiKey);
      
      if (hubspotSchools.length === 0) {
        setMessage({ text: 'No schools found in HubSpot', type: 'info' });
      } else {
        onSchoolsLoaded(hubspotSchools);
        setMessage({ 
          text: `Successfully imported ${hubspotSchools.length} schools from HubSpot`, 
          type: 'success' 
        });
        // Hide API key input after successful fetch
        setShowApiInput(false);
      }
    } catch (error) {
      setMessage({ 
        text: `Error fetching from HubSpot: ${error instanceof Error ? error.message : 'Unknown error'}`, 
        type: 'error' 
      });
    } finally {
      setLoading(false);
    }
  };

  // Import current schools to HubSpot
  const handleExportToHubSpot = async () => {
    if (!apiKey) {
      setMessage({ text: 'Please enter your HubSpot API key', type: 'error' });
      return;
    }
    
    if (schools.length === 0) {
      setMessage({ text: 'No schools to export', type: 'error' });
      return;
    }
    
    try {
      setLoading(true);
      setMessage({ text: 'Exporting schools to HubSpot...', type: 'info' });
      
      const success = await importSchoolsToHubSpot(apiKey, schools);
      
      if (success) {
        setMessage({ 
          text: `Successfully exported ${schools.length} schools to HubSpot`, 
          type: 'success' 
        });
        // Hide API key input after successful export
        setShowApiInput(false);
      } else {
        setMessage({ text: 'Failed to export schools to HubSpot', type: 'error' });
      }
    } catch (error) {
      setMessage({ 
        text: `Error exporting to HubSpot: ${error instanceof Error ? error.message : 'Unknown error'}`, 
        type: 'error' 
      });
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="w-full">
      <div className="flex flex-col md:flex-row justify-between md:items-center mb-3">
        <h3 className="text-lg font-semibold text-black">HubSpot Integration</h3>
        <button
          className="text-gray-700 text-sm hover:text-black hover:underline transition-colors flex items-center mt-1 md:mt-0"
          onClick={() => setShowApiInput(!showApiInput)}
        >
          {showApiInput ? (
            <>
              <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4 mr-1" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              </svg>
              Hide API Key
            </>
          ) : (
            <>
              <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4 mr-1" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M10.325 4.317c.426-1.756 2.924-1.756 3.35 0a1.724 1.724 0 002.573 1.066c1.543-.94 3.31.826 2.37 2.37a1.724 1.724 0 001.065 2.572c1.756.426 1.756 2.924 0 3.35a1.724 1.724 0 00-1.066 2.573c.94 1.543-.826 3.31-2.37 2.37a1.724 1.724 0 00-2.572 1.065c-.426 1.756-2.924 1.756-3.35 0a1.724 1.724 0 00-2.573-1.066c-1.543.94-3.31-.826-2.37-2.37a1.724 1.724 0 00-1.065-2.572c-1.756-.426-1.756-2.924 0-3.35a1.724 1.724 0 001.066-2.573c-.94-1.543.826-3.31 2.37-2.37.996.608 2.296.07 2.572-1.065z" />
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
              </svg>
              Configure HubSpot
            </>
          )}
        </button>
      </div>
      
      {showApiInput && (
        <div className="mb-4 p-4 border rounded-lg bg-white border-gray-200">
          <div className="mb-4">
            <label htmlFor="apiKey" className="block text-sm font-medium text-black mb-1">
              HubSpot API Key
            </label>
            <input
              type="password"
              id="apiKey"
              value={apiKey}
              onChange={(e) => setApiKey(e.target.value)}
              className="w-full p-2 border border-gray-300 rounded focus:ring-gray-500 focus:border-gray-500 bg-white text-black"
              placeholder="Enter your HubSpot API key"
            />
            <p className="mt-1 text-xs text-gray-700">
              Your API key is never stored on our servers and is only used for direct API calls.
            </p>
          </div>
        </div>
      )}
      
      {message && (
        <div 
          className={`p-3 rounded mb-4 flex items-start ${
            message.type === 'success' 
              ? 'bg-gray-100 text-black border border-gray-300' 
              : message.type === 'error'
                ? 'bg-gray-100 text-black border border-gray-300'
                : 'bg-gray-100 text-black border border-gray-300'
          }`}
        >
          <span className="mr-2 mt-0.5">
            {message.type === 'success' ? (
              <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4 text-green-700" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
              </svg>
            ) : message.type === 'error' ? (
              <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4 text-red-700" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              </svg>
            ) : (
              <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4 text-blue-700" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 16h-1v-4h-1m1-4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
            )}
          </span>
          <span className="flex-1">{message.text}</span>
          <button 
            onClick={() => setMessage(null)} 
            className="ml-2 text-gray-500 hover:text-gray-700"
          >
            <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
            </svg>
          </button>
        </div>
      )}
      
      <div className="flex flex-wrap gap-2">
        <button
          onClick={handleFetchFromHubSpot}
          className="px-4 py-2 bg-black text-white rounded hover:bg-gray-800 transition-colors disabled:opacity-50 flex items-center"
          disabled={!apiKey}
        >
          <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4 mr-1" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16v1a3 3 0 003 3h10a3 3 0 003-3v-1m-4-4l-4 4m0 0l-4-4m4 4V4" />
          </svg>
          Import from HubSpot
        </button>
        
        <button
          onClick={handleExportToHubSpot}
          className="px-4 py-2 bg-gray-700 text-white rounded hover:bg-gray-600 transition-colors disabled:opacity-50 flex items-center"
          disabled={!apiKey || schools.length === 0}
        >
          <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4 mr-1" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16v1a3 3 0 003 3h10a3 3 0 003-3v-1m-4-8l-4-4m0 0l-4 4m4-4v12" />
          </svg>
          Export to HubSpot
        </button>
      </div>
    </div>
  );
} 