import React, { useState } from 'react';
import { parseCSV } from '@/utils/csvParser';
import { School } from '@/types';

interface CSVUploadProps {
  onDataLoaded: (schools: School[]) => void;
  setLoading: (loading: boolean) => void;
}

export default function CSVUpload({ onDataLoaded, setLoading }: CSVUploadProps) {
  const [isDragging, setIsDragging] = useState(false);
  const [fileName, setFileName] = useState<string | null>(null);
  
  const handleDragOver = (e: React.DragEvent) => {
    e.preventDefault();
    setIsDragging(true);
  };
  
  const handleDragLeave = () => {
    setIsDragging(false);
  };
  
  const handleDrop = async (e: React.DragEvent) => {
    e.preventDefault();
    setIsDragging(false);
    
    const files = e.dataTransfer.files;
    if (files && files.length > 0) {
      await processFile(files[0]);
    }
  };
  
  const handleFileChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = e.target.files;
    if (files && files.length > 0) {
      await processFile(files[0]);
    }
  };
  
  const processFile = async (file: File) => {
    if (file.type !== 'text/csv' && !file.name.endsWith('.csv')) {
      alert('Please upload a CSV file');
      return;
    }
    
    setFileName(file.name);
    setLoading(true);
    
    try {
      const schools = await parseCSV(file);
      onDataLoaded(schools);
    } catch (error) {
      console.error('Error parsing CSV:', error);
      alert('Error parsing CSV file');
    } finally {
      setLoading(false);
    }
  };

  const handleSampleDownload = () => {
    window.open('/sample-schools.csv', '_blank');
  };
  
  return (
    <div className="w-full">
      <h3 className="font-semibold text-lg mb-2 text-black">Import School Data</h3>
      
      <div 
        className={`border-2 border-dashed p-6 rounded-lg text-center cursor-pointer transition-colors ${
          isDragging ? 'border-gray-700 bg-gray-50' : 'border-gray-300 hover:border-gray-400'
        }`}
        onDragOver={handleDragOver}
        onDragLeave={handleDragLeave}
        onDrop={handleDrop}
        onClick={() => document.getElementById('file-upload')?.click()}
      >
        <input 
          id="file-upload" 
          type="file" 
          accept=".csv" 
          className="hidden" 
          onChange={handleFileChange} 
        />
        
        <svg 
          xmlns="http://www.w3.org/2000/svg" 
          className="h-10 w-10 mx-auto mb-3 text-gray-400"
          fill="none" 
          viewBox="0 0 24 24" 
          stroke="currentColor"
        >
          <path 
            strokeLinecap="round" 
            strokeLinejoin="round" 
            strokeWidth={1.5} 
            d="M9 13h6m-3-3v6m5 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" 
          />
        </svg>
        
        {fileName ? (
          <div>
            <p className="text-black mb-1">File loaded: <span className="font-semibold">{fileName}</span></p>
            <p className="text-sm text-gray-700">Click or drop another file to replace</p>
          </div>
        ) : (
          <div>
            <p className="text-lg font-medium text-black mb-1">Drag and drop your CSV file here</p>
            <p className="text-sm text-gray-700 mb-2">or click to select a file</p>
            <button 
              onClick={(e) => {
                e.stopPropagation();
                handleSampleDownload();
              }}
              className="text-sm text-gray-700 hover:text-black hover:underline"
            >
              Download sample CSV format
            </button>
          </div>
        )}
      </div>
      
      <div className="mt-3 text-xs text-gray-700 bg-gray-50 p-3 rounded border border-gray-200">
        <p className="mb-1 font-medium text-black">Required CSV Columns:</p>
        <div className="grid grid-cols-2 md:grid-cols-3 gap-2">
          <div>• School Name</div>
          <div>• School District</div>
          <div>• Address</div>
          <div>• City</div>
          <div>• State</div>
          <div>• Zip Code</div>
        </div>
      </div>
    </div>
  );
} 